<template>
  <template v-if="stakersAddress === null">
    <div class="text-center">Please log into Metamask first, then refresh this website...</div>
  </template>

  <template v-if="loadingStakedNfts === true && loadingUnstakedNfts === true && stakersAddress !== null">
    <div class="text-center">Loading NFTs...</div>
  </template>

  <template v-if="collectionApprovedForStaking === false">
    <div class="text-center">
      <button @click="approveStaking" class="btn btn-primary">Approve Staking Contract</button>
    </div>
  </template>

  <div class="container">
    <div class="row">
      <div class="col-6">
        <h2 class="text-start">{{ projectname }}</h2>
        <div class="row">
          <div class="col-lg-4 col-sm-12 col-xs-12">
            <ul class="settings-menu">
              <button class="btn btn-primary" type="button" id="collection-filter" data-bs-toggle="dropdown"
                aria-powanded="false">
                Filter
              </button>
              <ul class="dropdown-menu" aria-labelledby="collection-filter">
                <li>
                  <a class="dropdown-item" href="#" @click="filterNfts('all')"
                    v-bind:class="filter === null ? 'current' : ''">All ({{ stakersNfts.length }})</a>
                </li>
                <li>
                  <a class="dropdown-item" href="#" @click="filterNfts('staked')"
                    v-bind:class="filter === 'staked' ? 'current' : ''">Staked ({{ stakedCount }})</a>
                </li>
                <li>
                  <a class="dropdown-item" href="#" @click="filterNfts('unstaked')"
                    v-bind:class="filter === 'unstaked' ? 'current' : ''">Unstaked ({{ unstakedCount }})</a>
                </li>
              </ul>


              <!-- &nbsp;
                <button class="btn btn-primary" type="button" id="rpc-selector" data-bs-toggle="dropdown"
                  aria-powanded="false">
                  rpc server
                </button>
                <ul class="dropdown-menu" aria-labelledby="rpc-selector">
                  <li>
                    <a class="dropdown-item" href="#" @click="setRpcIndex(0)"
                      v-bind:class="rpcIndex === 0 ? 'current' : ''">{{ rpcs[0] }}</a>
                  </li>
                  <li>
                    <a class="dropdown-item" href="#" @click="setRpcIndex(1)"
                      v-bind:class="rpcIndex === 1 ? 'current' : ''">{{ rpcs[1] }}</a>
                  </li>
                  <li>
                    <a class="dropdown-item" href="#" @click="setRpcIndex(2)"
                      v-bind:class="rpcIndex === 2 ? 'current' : ''">{{ rpcs[2] }}</a>
                  </li>
                  <li>
                    <a class="dropdown-item" href="#" @click="setRpcIndex(3)"
                      v-bind:class="rpcIndex === 3 ? 'current' : ''">{{ rpcs[3] }}</a>
                  </li>
                  <li>
                    <a class="dropdown-item" href="#" @click="setRpcIndex(4)"
                      v-bind:class="rpcIndex === 4 ? 'current' : ''">{{ rpcs[4] }}</a>
                  </li>
                  <li>
                    <a class="dropdown-item" href="#" @click="setRpcIndex(5)"
                      v-bind:class="rpcIndex === 5 ? 'current' : ''">{{ rpcs[5] }}</a>
                  </li>
                </ul> -->

              &nbsp;
              <button v-if="showLevelingExplanation && stakersPowBalance > 0" @click="showLevelingExplanation = false"
                class="btn btn-primary">
                Hide Explanation
              </button>
              <button v-if="!showLevelingExplanation && stakersPowBalance > 0" @click="showLevelingExplanation = true"
                class="btn btn-primary">
                Show Explanation
              </button>
            </ul>
          </div>
        </div>
      </div>
    </div>





