    <template v-if="transactionInProgress">
      <div class="container">
        <div class="row">
          <div class="col-12 text-center">
            Waiting for current transaction to finish...
          </div>
        </div>
      </div>
    </template>

    <div class="row text-center">
      <h4>{{ feedbackMessage }}</h4>
    </div>
    <br />


    <div v-if="stakersPowAllowanceForLeveling <= 0" class="row">
      <div class="col-xxl-6">
        <div class="card top-bid">
          <div class="card-body">
            <div class="row align-items-center">
              <div class="col align-items-center">
                <span>Before you can upgrade your NFTs, you need to approve the POW token once.</span>
                <button class="btn btn-primary" type="button" id="approvePowToken" @click="approvePowToken">
                  approve POW
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>


    <div v-if="showLevelingExplanation || stakersPowBalance <= 0" class="row">
      <div class="col-xxl-6">
        <div class="card top-bid">
          <div class="card-body">
            <div class="row align-items-center">
              <div class="col align-items-center">
                <h4>
                  NFT Level Upgrades
                </h4>
                <ul>
                  <li>You can permanently increase the staking power of your NFTs by upgrading the levels of any NFT you
                    own.
                  </li>
                  <li>For level upgrades you need POW tokens which you can buy in the <a
                      href="https://linkmissing/pow">POW Shop</a>.
                  </li>
                  <!-- <span>Each level upgrade increases the staking power of your upgraded NFTs permanently. </span> -->
                  <li>Each level upgrade costs <strong>100 POW tokens. </strong>
                  </li>
                  <li>The maximum level is <strong>{{ maxLevel }}. </strong>
                  </li>
                  <li>Use the slider to select the level you want to upgrade to. The website shows you a preview of the
                    power after the upgrade.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-xxl-6">
        <div class="card top-bid">
          <div class="card-body">
            <div class="row align-items-center">
              <div class="col align-items-center">
                <h4>Automatic Harvests and Pure Harvests</h4>
                <ul>
                  <li>Whenever you stake, unstake or upgrade one or multiple NFTs, a harvest of WOM reward tokens is
                    automatically executed.
                  </li>
                  <li>Additionally, you can harvest WOM reward tokens whenever you want. This operation is called a
                    <strong>pure harvest.</strong>
                  </li>
                </ul>
                <br/>
                <h4>Harvest Penalties and Bonuses</h4>
                <h6>Penalties</h6>
                <ul>
                  <li>If you harvest within the first 10 days of staking, the amount is reduced with a penalty.
                  </li>
                  <li>
                    The penalty applies to both automatic and pure harvests.
                  </li>
                  <li>
                    Within the first day of staking, the penalty is 90% of the harvest amount.
                  </li>
                  <li>
                    For each subsequent full day of
                    staking, the penalty percentage is reduced by 10%.
                  </li>
                  <li>
                    That means, you will receive 20% after a full
                    day of staking, 30% after 2 full days and so on.
                  </li>
                  <li>
                    After 9 full days of staking your NFTs, you will
                    receive 100% of the harvest amount (no penalties).
                  </li>
                </ul>
                <br/>
                <h6>Bonus</h6>
                <ul>
                  <li>But wait, that's not all: After 20 full days of staking, you will receive a 20% bonus. 
                  </li>
                  <li>Note that each pure harvest and each unstake will reset the penalties and bonuses cycle. A stake
                    or level upgrade will <strong>not</strong> reset the cycle.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="col-12">
      <div class="card filter-tab">
        <div class="card-body bs-0 p-0 bg-transparent">
          <div class="row">
            <div class="col-xxl-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
              <div class="card-body">
                <h4 v-if="selectedNfts.length == 1">1 NFT selected
                  -- Balance: {{ stakersPowBalance }} POW
                  &nbsp;
                  <i title="Refresh POW Balance" @click="refreshPowBalance" class="ri-refresh-line"></i>
                </h4>
                <h4 v-else>{{ selectedNfts.length }} NFTs selected
                  -- Balance: {{ stakersPowBalance }} POW
                  <i title="Refresh POW Balance" @click="refreshPowBalance" class="ri-refresh-line"></i>
                </h4>
                <button @click="selectVisible" class="btn btn-success" style="font-size : 14px;">
                  Select all
                </button>
                &nbsp;
                <button @click="clearSelection" class="btn btn-danger" style="font-size : 14px;">
                  Clear selection
                </button>
                &nbsp;
                <button @click="upgradeSelectedNfts()" class="btn btn-success"
                  :disabled="selectedNfts.length <= 0 || stakersPowBalance <= 0 || collectionApprovedForStaking === false || paused"
                  style="font-size : 14px;">
                  Upgrade {{ Math.min(selectedNfts.length, maxNftsPerTx) }} for {{ powCostToBulkUpgrade }}
                  POW
                </button>
                &nbsp;
                <button @click="stakeSelected" class="btn btn-success"
                  :disabled="selectedNfts.length <= 0 || collectionApprovedForStaking === false || paused"
                  style="font-size : 14px;">
                  Stake {{ Math.min(selectedNfts.length, maxNftsPerTx) }}
                </button>
                &nbsp;
                <button @click="unstakeSelected" class="btn btn-danger"
                  :disabled="selectedNfts.length <= 0 || collectionApprovedForStaking === false || paused"
                  style="font-size : 14px;">
                  Unstake {{ Math.min(selectedNfts.length, maxNftsPerTx) }}
                </button>
                <p v-if="selectedNfts.length > maxLevel">Note: {{ maxLevel }} NFTs max staking and unstaking in 1
                  transaction</p>
              </div>
            </div>
          </div>

          <br />

          <div class="row">
            <template v-if="
              collectionApprovedForStaking !== null &&
              loadingStakedNfts === false &&
              loadingUnstakedNfts === false &&
              stakersNfts.length <= 0
            ">
              <h1 class="text-center">No NFTs to display</h1>
            </template>



            <div class="col-xxl-3 col-xl-4 col-lg-4 col-md-6 col-sm-6" v-for="tokenId in filteredNfts" :key="tokenId">
              <div class="card items">
                <div class="card-body">
                  <div class="items-img position-relative">
                    <img :src="nftInfo[tokenId].image" class="img-fluid rounded mb-3" alt=""
                      @click="selectNft(tokenId)" />
                  </div>
                  <input type="checkbox" :value="tokenId" v-model="selectedNfts" @click="selectNft(tokenId)">
                  &nbsp;
                  <label>
                    <h4 class="card-title">
                      #{{ tokenId }}
                      &nbsp;
                      <span title="current level">{{ nftInfo[tokenId].level }}</span>
                      &nbsp;
                      <span title="current staking power">{{ powerForLevel[nftInfo[tokenId].level] }}</span>
                    </h4>
                  </label>
                  <div class="d-flex justify-content-center mt-3">
                    <button v-if="nftInfo[tokenId].staked" @click="unstake(tokenId)" class="btn btn-danger"
                      :disabled="paused">
                      Unstake
                    </button>
                    <button v-else @click="stake(tokenId)" class="btn btn-primary"
                      :disabled="collectionApprovedForStaking === false || paused">
                      Stake
                    </button>
                  </div>

                  <div v-if="nftInfo[tokenId].level >= maxLevel">
                    Max. level reached! 
                  </div>
                  <div v-else>
                    <div>
                      <span title="current level">{{ nftInfo[tokenId].level }}</span>
                      <input type="range" title="Choose desired level after upgrade" class="form-control-range"
                        :min="nftInfo[tokenId].level + 1" :max="maxLevel" id="levelsToUpgradeSlider"
                        v-model="nftInfo[tokenId].nftLevelAfterUpgrade" @change="updateLevelsToUpgrade(tokenId)">
                      <span title="level after upgrade" class="text-muted"
                        v-if="nftInfo[tokenId].nftLevelAfterUpgrade <= maxLevel"> ({{
                            nftInfo[tokenId].nftLevelAfterUpgrade
                        }})
                      </span>
                      <span class="text-muted" title="power after upgrade">
                        ({{
                            powerForLevel[nftInfo[tokenId].nftLevelAfterUpgrade]
                        }})</span>
                    </div>
                    <button @click="upgradeSpecificNft(tokenId, nftInfo[tokenId].levelsToUpgrade)"
                      class="btn btn-primary"
                      :disabled="stakersPowBalance <= 0 || stakersPowAllowanceForLeveling <= 0 || nftInfo[tokenId].level >= maxLevel || collectionApprovedForStaking === false || paused"
                      style="font-size : 14px;">
                      Upgrade {{ nftInfo[tokenId].levelsToUpgrade }} level<template
                        v-if="nftInfo[tokenId].levelsToUpgrade != 1">s</template> for {{
                            nftInfo[tokenId].levelsToUpgrade
                            * powCostPerLevel
                        }} POW
                    </button>
                  </div>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>


const Web3 = require("web3");
import { ethers } from "ethers";
import NftStaking from "../../artifacts/contracts/NftStaking.sol/NftStaking.json";
import WorldOfMasksNFT from "../../artifacts/contracts/WorldOfMasksNFT.sol/WorldOfMasksNFT.json";
import PowToken from "../../artifacts/contracts/PowToken.sol/PowToken.json";


// TODO Testnet - Mainnet switch
// import womInitialStakes from "../wom-initial-stakes.json";
// import womLevel from "../wom-level.json";


// TODO Testnet - Mainnet switch
import womInitialStakesTest from "../womTest-initial-stakes.json";

import womTestLevel from "../womTest-level.json";




// TODO Testnet - Mainnet switch
