export default {
  name: "App",
  props: ["nftCA", "projectname"],
  emits: ['updateStakingPowerEvent'],

  data() {
    return {
      rpc: null,

      maxLevel: 100,
      powCostPerLevel: 100,
      powCostToBulkUpgrade: 0,

      powerForLevel: [
        2000,
        2300,
        2627,
