<template>
  <div class="col-12 align-items-center">
    <div class="row align-items-center">
      <h4>
        Balance: {{ stakersWomBalance }} WoM &nbsp;
        <i title="Refresh POW Balance" @click="loadWomBalance" class="ri-refresh-line"></i>
      </h4>
      <br />
      <h4 v-on:updateStakingPowerEvent="loadStakerInfo">
        Staking power: {{ stakersPower }} &nbsp;
        <i title="Refresh POW Balance" @click="loadStakerInfo" class="ri-refresh-line"></i>
      </h4>
      <br />

      <template v-if="stakersPower > 0">
        <h4>
          Staked for {{ stakerStakedForSeconds }} seconds. (Harvest and unstake reset the timer.)
        </h4>
        <br />
        <h4>
          Current payout level: {{ stakersPayoutPct }}%
        </h4>
        <br />
        <h4 v-if="stakerNextPayoutPctInSeconds > 0">
          Next payout level: {{ stakersNextPayoutPct }}% -- reached in {{ stakerNextPayoutPctInSeconds }} seconds
        </h4>
        <h4 v-else>
          Congratulations! You reached the highest payout level. 
        </h4>
      </template>

      <br />
      <h6>Total staked power: {{ totalStakedPower }}</h6>
      <h6>Base power: {{ baseStakingPower }}</h6>
      <h6>Total WoM Emission: ~{{
          Math.round(
            womEmissionPerHour * earningsTime * 100) / 100
      }}
        WoM / {{ hoursInWords[earningsTime] }}</h6>
      <br />
      <div class="settings-menu" v-if="womEmissionPerHour && totalStakedPower && stakersPower">
        <button class="btn btn-primary" type="button" id="collection-filter" data-bs-toggle="dropdown"
          aria-expanded="false" style="font-size : 14px;">
          Earning ~{{
              Math.round(
                stakerEarningRateX1k *
                earningsTime) / 1000
          }}
          WoM / {{ hoursInWords[earningsTime] }}
        </button>
        <ul class="dropdown-menu" aria-labelledby="earnings-time">
          <li>
            <a class="dropdown-item" @click="earningsTime = 1" :class="earningsTime === 1 ? 'current' : ''">
              Earning ~{{
                  Math.round(
                    stakerEarningRateX1k *
                    1) / 1000
              }}
              WoM / hour
            </a>
          </li>
          <li>
            <a class="dropdown-item" @click="earningsTime = 24" v-bind:class="
              earningsTime === 24 ? 'current' : ''
            ">
              Earning ~{{
                  Math.round(
                    stakerEarningRateX1k *
                    24) / 1000
              }}
              WoM / day
            </a>
          </li>
          <li>
            <a class="dropdown-item" @click="earningsTime = 168" v-bind:class="
              earningsTime === 168 ? 'current' : ''
            ">
              Earning ~{{
                  Math.round(
                    stakerEarningRateX1k *
                    168
                  ) / 1000
              }}
              WoM / week
            </a>
          </li>
          <li>
            <a class="dropdown-item" @click="earningsTime = 720" v-bind:class="
              earningsTime === 720 ? 'current' : ''
            ">
              Earning ~{{
                  Math.round(
                    stakerEarningRateX1k *
                    720
                  ) / 1000
              }}
              WoM / month
            </a>
          </li>
          <li>
            <a class="dropdown-item" @click="earningsTime = 8760" v-bind:class="
              earningsTime === 8760 ? 'current' : ''
            ">
              Earning ~{{
                  Math.round(
                    stakerEarningRateX1k *
                    8760
                  ) / 1000
              }}
              WoM / year
            </a>
          </li>
        </ul>
      </div>
      <div class="settings-menu">
        <button class="btn btn-primary" @click="harvest" :disabled="paused || womToHarvest == 0" style="font-size : 14px;">
          Harvest {{ Math.round(stakersPayoutPct * womToHarvest * 10) / 1000 }} WoM
        </button>
      </div>
    </div>
  </div>
</template>


<script>
import { ethers } from "ethers";
import NftStaking from "../../artifacts/contracts/NftStaking.sol/NftStaking.json";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";

let SECONDS_PER_DAY = 60;


// let RewardTokenAddress = window.RewardTokenAddress;
// let StakingAddress = window.StakingAddress;

export default {
  name: "App",
  data() {
    return {
      rewardTokenContract: null,

      nftContract: null,
      stakingContract: null,

      provider: null,
      signer: null,

      womEmissionPerHour: null,

      stakersAddress: null,
      stakerStakedForSeconds: null,
      stakersPayoutPct: null,
      stakersNextPayoutPct: null,
      stakerNextPayoutPctInSeconds: null,

      stakersPower: null,
      totalStakedPower: null,
      baseStakingPower: null,
      stakerEarningRateX1k: null,

      earningsTime: 168,
      hoursInWords: {
        1: "hour",
        24: "day",
        168: "week",
        720: "month",
        8760: "year"
      },

      womToHarvest: null,
      paused: true,
      stakersWomBalance: 0,
    };
  },
  methods: {
    harvest: async function () {
      let tx = await this.stakingContract.harvest();
      await tx.wait();
      this.load();
    },
    approve: async function () {
      let tx = await this.nftContract.setApprovalForAll(window.StakingAddress, true);
      await tx.wait();
      window.location.reload();
    },

    loadStakerInfo: function () {
      this.stakingContract.showStakerInfo(this.stakersAddress).then((stakerInfo) => {
        let stakedForSeconds = stakerInfo[0];
        this.stakerStakedForSeconds = stakedForSeconds;
        if (stakedForSeconds > 20 * SECONDS_PER_DAY) {
          this.stakersPayoutPct = 120; // 20% bonus
          this.stakersNextPayoutPct = 120;
          this.stakerNextPayoutPctInSeconds = 0;
        } else if (stakedForSeconds < 9 * SECONDS_PER_DAY) {
          let stakedForDays = Math.floor(stakedForSeconds / SECONDS_PER_DAY);
          this.stakersPayoutPct = (stakedForDays + 1) * 10;
          this.stakersNextPayoutPct = this.stakersPayoutPct + 10;
          this.stakerNextPayoutPctInSeconds = (stakedForDays + 1) * SECONDS_PER_DAY - stakedForSeconds;
        } else {
          this.stakersPayoutPct = 100;
          this.stakersNextPayoutPct = 120;
          this.stakerNextPayoutPctInSeconds = 20 * SECONDS_PER_DAY - stakedForSeconds;
        }

        this.stakersPower = stakerInfo[1];
        this.totalStakedPower = stakerInfo[2];
        this.baseStakingPower = stakerInfo[3];
        this.womEmissionPerHour =
          Math.round(
            ethers.utils.formatEther(stakerInfo[4]) * 100000
          ) / 100000;
        this.stakerEarningRateX1k = this.womEmissionPerHour * this.stakersPower / (this.totalStakedPower - this.baseStakingPower) * 1000;
      });
      this.womToHarvest = 0;
      this.stakingContract.showStakersEarningsUntilLastTimestamp(this.stakersAddress).then((amount) => {
        this.womToHarvest +=
          Math.round(
            ethers.utils.formatEther(amount) * 100000
          ) / 100000;
      });
      this.stakingContract.showStakersEarningsAfterLastTimestamp(this.stakersAddress).then((earnings) => {
        this.womToHarvest +=
          Math.round(
            ethers.utils.formatEther(earnings) * 100000
          ) / 100000;
      });
      this.stakingContract.isPaused().then((pause) => {
        this.paused = pause;
      });
    },

    loadWomBalance: function () {
      console.log(this.stakersAddress);
      this.rewardTokenContract.balanceOf(this.stakersAddress).then((balance) => {
        this.stakersWomBalance = Math.round(ethers.utils.formatEther(balance) * 100000) / 100000;
      });
    },




    load: function () {
      this.signer.getAddress().then((userAddress) => {
        this.stakersAddress = userAddress;
        this.loadStakerInfo();
        this.loadWomBalance();
      });

    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();
        let _womContract = new ethers.Contract(
          window.RewardTokenAddress,
          RewardToken.abi,
          this.provider.getSigner()
        );
        let _stakingContract = new ethers.Contract(
          window.StakingAddress,
          NftStaking.abi,
          this.provider.getSigner()
        );

        this.rewardTokenContract = _womContract;
        this.stakingContract = _stakingContract;
      })
      .then(() => {
        this.load();
      });
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 60px;
}
</style>
