<template>
  <div class="content-body">
    <div class="container">
      <div class="row">
        <div class="col-xxl-12">
          <div>
            <h2 class="mb-0">NFT Staking Statistics</h2>
            <p>Earnable WoM as rewards: 25 million in total, 12.5 million over first 3 years</p>
            <p>WoM total max. supply: 50 million</p>
            <p>Total staked power: {{ totalStakedPower }}</p>
            <p>Base staking power: {{ baseStakingPower }}</p>
            <!-- <p>Released WoM by contract: {{ released }}</p> -->
            <p>Total WoM emission rate: {{ womEmissionPerHour }} WoM / hour</p>
            <p> = {{ Math.round(womEmissionPerHour * 24 * 365 / 1000) / 1000 }} million WoM / year </p>

            <h1 class="mb-0">---Everything below this line is ADMIN ONLY---</h1>
            <h2 v-if="!isAdmin">(You are not logged into the admin wallet)</h2>
            <h2 v-else>(YOU ARE THE ADMIN)</h2>

            <h2 class="mb-0">Register Staking Contract</h2>
            <div> Staking is minter: <strong>{{ stakingIsWomMinter }}</strong> </div>
            <div>
              <button class="btn btn-danger" type="button" :disabled="!stakingIsWomMinter"
                @click="unregisterStakingAsMinter">Unregister as minter</button>
                &nbsp;
              <button class="btn btn-success" type="button" :disabled="stakingIsWomMinter"
                @click="registerStakingAsMinter">Register as minter</button>
            </div>
            <br />

            <h2 class="mb-0">Pause Staking Contract</h2>
            Staking is active: {{ !stakingPaused }}
            <p>
              <button @click="stakeAdminPause" :disabled="stakingPaused" class="btn btn-danger">Pause</button>
              &nbsp;
              <button @click="stakeAdminUnpause" :disabled="!stakingPaused"
                class="btn btn-success">Unpause</button>
            </p>
            <!-- <p>
              <button @click="resetBoosts" class="btn btn-warning">Reset boosts</button>
            </p> -->
          </div>
          <br />



          <div>
            <h2 class="mb-0">POW Token Shop Admin Control</h2>
            <h4>Current POW Price</h4>
            <div> 1 POW costs: <strong>{{ powTokenPriceBch / 10 ** 18 }} BCH</strong> </div>
            <div>
              <input type="number" v-model="powTokenPriceBch" size="18" />
              &nbsp;
              <button class="btn btn-warning" type="button" @click="setNewPriceBchForPow">Set
                New BCH Price</button>
            </div>
            <div> 1 POW costs: <strong>{{ powTokenPriceWom / 10 ** 18 }} WoM</strong> </div>
            <div>
              <input type="number" v-model="powTokenPriceWom" size="18" />
              &nbsp;
              <button class="btn btn-warning" type="button" @click="setNewPriceWomForPow">Set
                New WoM Price</button>
            </div>
            <div> 1 POW costs: <strong>{{ powTokenPriceFlexUsd / 10 ** 18 }} flexUSD</strong> </div>
            <div>
              <input type="number" v-model="powTokenPriceFlexUsd" size="18" />
              &nbsp;
              <button class="btn btn-warning" type="button" @click="setNewPriceFlexUsdForPow">Set
                New flexUSD Price</button>
            </div>
            <h6>Note: Setting price to 0 means that the POW token seller contract stops accepting the token as payment.
            </h6>
            <br />
            <h4>POW Token Seller Contract Balances</h4>
            <p>{{ Math.round(powTokenSellerBchBalance / 10 ** 15) / 10 ** 3 }} BCH</p>
            <p>{{ Math.round(powTokenSellerWomBalance / 10 ** 15) / 10 ** 3 }} WoM</p>
            <p>{{ Math.round(powTokenSellerFlexUsdBalance / 10 ** 15) / 10 ** 3 }} flexUSD</p>
            <div>
              <button class="btn btn-primary" type="button" :disabled="powTokenSellerBchBalance <= 0"
                @click="withdrawBchFromPowTokenSeller">withdraw BCH</button>
                &nbsp;
              <button class="btn btn-primary" type="button" :disabled="powTokenSellerWomBalance <= 0"
                @click="withdrawWoMFromPowTokenSeller">withdraw
                WoM</button>
                &nbsp;
              <button class="btn btn-primary" type="button" :disabled="powTokenSellerFlexUsdBalance <= 0"
                @click="withdrawFlexUsdFromPowTokenSeller">withdraw
                flexUSD</button>
            </div>
            <br />
            <h4>POW Token Seller has to be POW token minter </h4>
            <div> TokenSeller is minter: <strong>{{ powSellerIsPowMinter }}</strong> </div>
            <div>
              <button class="btn btn-danger" type="button" :disabled="!powSellerIsPowMinter"
                @click="unregisterPowSellerAsMinter">Unregister as minter</button>
                &nbsp;
              <button class="btn btn-success" type="button" :disabled="powSellerIsPowMinter"
                @click="registerPowSellerAsMinter">Register as minter</button>
            </div>
            <br />


            <h4>Pause and Resume POW Token Seller</h4>
            <div> Token Sale active: <strong>{{ powSaleActive }}</strong> </div>
            <div>
              <button class="btn btn-danger" type="button" :disabled="!powSaleActive" @click="powSellerPauseSale">Pause
                Sale</button>
                &nbsp;
              <button class="btn btn-success" type="button" :disabled="powSaleActive"
                @click="powSellerResumeSale">Resume
                Sale</button>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>

import { ethers } from "ethers";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";
import NftStaking from "../../artifacts/contracts/NftStaking.sol/NftStaking.json";
import PowTokenSeller from "../../artifacts/contracts/PowTokenSeller.sol/PowTokenSeller.json";
import PowToken from "../../artifacts/contracts/PowToken.sol/PowToken.json";


export default {
  name: 'App',
  components: {},
  data() {
    return {
      rewardTokenContract: null,
      stakingContract: null,
      powSellerContract: null,
      powTokenContract: null,

      powSaleActive: false,
      powTokenPriceBch: 0,
      powTokenPriceWom: 0,
      powTokenPriceFlexUsd: 0,

      powTokenSellerBchBalance: 0,
      powTokenSellerWomBalance: 0,
      powTokenSellerFlexUsdBalance: 0,

      stakingIsWomMinter: false,
      powSellerIsPowMinter: false,

      isAdmin: false,

      stakingPaused: false,
      totalStakedPower: null,
      baseStakingPower: null,
      released: null,
      womEmissionPerHour: null,
    }
  },
  methods: {
    getPowSaleActive: async function () {
      this.powSellerContract.saleActive().then((isActive) => {
        this.powSaleActive = isActive;
      });
    },
    getPowTokenPrices: async function () {
      this.powSellerContract.powPriceBch().then((priceInWei) => {
        this.powTokenPriceBch = priceInWei;
      });
      this.powSellerContract.getPowPriceToken(window.RewardTokenAddress).then((priceInWei) => {
        this.powTokenPriceWom = priceInWei;
      });
      this.powSellerContract.getPowPriceToken(window.FlexUsdAddress).then((priceInWei) => {
        this.powTokenPriceFlexUsd = priceInWei;
      });
    },

    withdrawBchFromPowTokenSeller: async function () {
      let tx = await this.powSellerContract.withdrawBch();
      await tx.wait();
      window.location.reload();
    },
    withdrawWoMFromPowTokenSeller: async function () {
      let tx = await this.powSellerContract.withdrawToken(window.RewardTokenAddress);
      await tx.wait();
      window.location.reload();
    },
    withdrawFlexUsdFromPowTokenSeller: async function () {
      let tx = await this.powSellerContract.withdrawToken(window.FlexUsdAddress);
      await tx.wait();
      window.location.reload();
    },

    setNewPriceBchForPow: async function () {
      let tx = await this.powSellerContract.setNewPriceBch(this.powTokenPriceBch.toString());
      await tx.wait();
      window.location.reload();
    },
    setNewPriceWomForPow: async function () {
      let tx = await this.powSellerContract.setNewPriceToken(window.RewardTokenAddress, this.powTokenPriceWom.toString());
      await tx.wait();
      window.location.reload();
    },
    setNewPriceFlexUsdForPow: async function () {
      let tx = await this.powSellerContract.setNewPriceToken(window.FlexUsdAddress, this.powTokenPriceFlexUsd.toString());
      await tx.wait();
      window.location.reload();
    },

    powSellerPauseSale: async function () {
      let tx = await this.powSellerContract.pauseSale();
      await tx.wait();
      window.location.reload();
    },
    powSellerResumeSale: async function () {
      let tx = await this.powSellerContract.resumeSale();
      await tx.wait();
      window.location.reload();
    },

    registerStakingAsMinter: async function () {
      let tx = await this.rewardTokenContract.addMinter(window.StakingAddress);
      await tx.wait();
      window.location.reload();
    },
    unregisterStakingAsMinter: async function () {
      let tx = await this.rewardTokenContract.removeMinter(window.StakingAddress);
      await tx.wait();
      window.location.reload();
    },
    registerPowSellerAsMinter: async function () {
      let tx = await this.powTokenContract.addMinter(window.PowSellerAddress);
      await tx.wait();
      window.location.reload();
    },
    unregisterPowSellerAsMinter: async function () {
      let tx = await this.powTokenContract.removeMinter(window.PowSellerAddress);
      await tx.wait();
      window.location.reload();
    },

    stakeAdminPause: async function () {
      let tx = await this.stakingContract.setPaused(true);
      await tx.wait();
      window.location.reload();
    },
    stakeAdminUnpause: async function () {
      let tx = await this.stakingContract.setPaused(false);
      await tx.wait();
      window.location.reload();
    },

    // resetBoosts: async function () {
    //   let tx = await this.stakingContract.resetBoosts();
    //   await tx.wait();
    //   window.location.reload();
    // },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    this.provider.send("eth_requestAccounts", []).then(() => {
      this.signer = this.provider.getSigner();
      let _rewardTokenContract = new ethers.Contract(window.RewardTokenAddress, RewardToken.abi, this.provider.getSigner());
      let _stakingContract = new ethers.Contract(window.StakingAddress, NftStaking.abi, this.provider.getSigner());
      let _powSellerContract = new ethers.Contract(window.PowSellerAddress, PowTokenSeller.abi, this.provider.getSigner());
      let _powTokenContract = new ethers.Contract(window.PowTokenAddress, PowToken.abi, this.provider.getSigner());
      let _flexUsdContract = new ethers.Contract(window.FlexUsdAddress, RewardToken.abi, this.provider.getSigner());

      this.rewardTokenContract = _rewardTokenContract;
      this.stakingContract = _stakingContract;
      this.powSellerContract = _powSellerContract;
      this.powTokenContract = _powTokenContract;
      this.flexUsdTokenContract = _flexUsdContract;
    }).then(() => {
      this.signer.getAddress().then((address) => {
        if (address === window.DevAddress){
          this.isAdmin = true;
        }
      });
      this.stakingContract.showStakerInfo(window.DevAddress).then((stakerInfo) => {
        this.totalStakedPower = stakerInfo[2];
        this.baseStakingPower = stakerInfo[3];
        this.womEmissionPerHour =
          Math.round(
            ethers.utils.formatEther(stakerInfo[4]) * 100000
          ) / 100000;
      });
      this.stakingContract.isPaused().then((isPaused) => {
        this.stakingPaused = isPaused;
      });
      this.powTokenContract.isMinter(window.PowSellerAddress).then((_isMinter) => {
        this.powSellerIsPowMinter = _isMinter;
      });
      this.provider.getBalance(window.PowSellerAddress).then((balance) => {
        this.powTokenSellerBchBalance = balance;
      });
      this.rewardTokenContract.isMinter(window.StakingAddress).then((_isMinter) => {
        this.stakingIsWomMinter = _isMinter;
      });
      this.rewardTokenContract.balanceOf(window.PowSellerAddress).then((balance) => {
        this.powTokenSellerWomBalance = balance;
      });
      this.flexUsdTokenContract.balanceOf(window.PowSellerAddress).then((balance) => {
        this.powTokenSellerFlexUsdBalance = balance;
      });
      this.getPowSaleActive();
      this.getPowTokenPrices();
    })
  }
}
</script>