<template>
  <section class="text-center container col-lg-8 col-md-9 col-sm-10 mx-auto mt-2 mb-4">
    <div class="row py-3">
      <div>
        <h1 class="fw-light"><strong><span>The POW Token Shop</span></strong></h1>

        <div class="lead text-muted"><span>You can increase the staking power of your WoMlub NFTs with POW tokens.
          </span></div>


        <div class="lead text-muted"><span>For {{ powTokenPriceBch / 10 ** 18 }} BCH you can get 1 POW token.
          </span></div>

        <h4 class="lead text-muted mx-auto my-3"><span>Please select how many
            POW tokens you want.</span></h4>


        <h4 class="lead mx-auto my-4"><span>Note that the transaction cannot be undone.</span></h4>
      </div>



      <div class="mb-3 mx-auto">
        <div class="input-group mx-auto">
          <div class="input-group-prepend mx-auto">
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 10">Min</button>
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 25">25</button>
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 50">50</button>
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 100">100</button>
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 250">250</button>
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 500">500</button>
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 1000">1000</button>
            <button class="btn btn-outline-secondary" type="button" @click="powTokensToBuy = 10000">10,000</button>
          </div>
        </div>
        <div class="col-lg-4 col-md-6 col-sm-8 mx-auto">
          <input id="tokensToBuy" type="number" min="10" class="form-control mx-auto" v-model="powTokensToBuy">
        </div>
      </div>

      <span class="row">
        <span class="column">
          <h4>Buy {{ powTokensToBuy }} POW for:</h4>
          <div class="col-lg-6 col-md-8 mx-auto">
            <button id="buyButtonBch" type="button" class="btn btn-primary my-2 btn-large" :disabled="!powSaleActive || powTokenPriceBch <= 0"
              @click="buyPowWithBch">{{ Math.round(powTokensToBuy * powTokenPriceBch / 10 ** 12) / 10**6  }} BCH</button>
            &nbsp;
            <button id="buyButtonWom" type="button" class="btn btn-primary my-2 btn-large" :disabled="!powSaleActive || powTokenPriceWom <= 0"
              @click="buyPowWithToken('WoM')">{{ Math.round(powTokensToBuy * powTokenPriceWom / 10 ** 12) / 10**6 }} WoM</button>
            &nbsp;
            <button id="buyButtonFlexUsd" type="button" class="btn btn-primary my-2 btn-large"
              :disabled="!powSaleActive || powTokenPriceFlexUsd <= 0" @click="buyPowWithToken('flexUSD')">{{ Math.round(powTokensToBuy * powTokenPriceFlexUsd
                  / 10**12) / 10**6 }} flexUSD</button>
          </div>
          <div class="lead text-muted"><span>Your Balances: {{ Math.round(stakersBchBalance / 10**15) / 10**3 }} BCH, {{ Math.round(stakersWomBalance/10**15) / 10**3 }} WoM,
              {{ Math.round(stakersFlexUsdBalance/10**15)/10**3 }} flexUSD
            </span></div>
        </span>
      </span>
    </div>

    <div class="row">
      <div class="text-monospace text-muted col-lg-12 col-md-12 mx-auto mt-1 mb-2">{{ feedbackMessage }}</div>
    </div>
    <br />
    <h1 class="fw-light"><strong>Your POW tokens</strong></h1>

    <div class="lead text-muted"><span>Balance: {{ stakersPowBalance }} POW
      </span></div>
    <div class="lead text-muted" v-if="stakersPowBalance > 0 && stakersPowAllowanceForLeveling <= 0"><span>Remember to
        approve the POW token for level upgrades.</span></div>
    <div class="lead text-muted" v-if="stakersPowBalance > 0 && stakersPowAllowanceForLeveling > 0"><span>You are now
        ready to level up some NFTs!</span></div>

    <br />
    <div class="row py-5">
      <div>
        <h1 class="fw-light"><strong>POW Tokens Approval for Leveling</strong></h1>

        <div class="lead text-muted"><span>Before you can upgrade the staking power of the NFTs, you have to approve the
            leveler contract as a
            spender of POW tokens.</span></div>

        <div class="lead text-muted"><span>You only need to give your approval once.</span></div>

        <div class="lead text-muted"><span>If you wish to remove your approval, you can do that here, too.</span></div>

        <div class="col-12">
          <button class="btn btn-success" type="button" id="approvePowToken"
            :disabled="stakersPowAllowanceForLeveling > 0" @click="approvePowToken">
            approve
          </button>
          &nbsp;
          <button class="btn btn-danger" type="button" id="removePowAllowance"
            :disabled="stakersPowAllowanceForLeveling <= 0" @click="removePowTokenAllowance">
            (remove approval)
          </button>
        </div>
      </div>
    </div>
    <!-- <br />
    <div class="row">
      <h5>For testing: Change POW token allowance</h5>
      <div class="lead text-muted"><span>Allowance for Leveling: {{ stakersPowAllowanceForLeveling }} POW
      </span></div>
      <div class="lead text"><span>Use the minus or plus button to lower or raise your POW token allowance by the amount in the number input box above.
      </span></div>
      <div class="col-12">

        <button class="btn btn-primary" type="button" id="decreasePowAllowance" @click="decreasePowTokenAllowance">
          -
        </button>
        <button class="btn btn-primary" type="button" id="increasePowAllowance" @click="increasePowTokenAllowance">
          +
        </button>
      </div>
    </div> -->
  </section>

</template>

<script>

import { ethers } from "ethers";
import PowTokenSeller from "../../artifacts/contracts/PowTokenSeller.sol/PowTokenSeller.json";
import PowToken from "../../artifacts/contracts/PowToken.sol/PowToken.json";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";



export default {
  name: 'App',
  components: {},
  data() {
    return {
      provider: null,
      signer: null,

      stakersAddress: "",

      powTokenContract: null,
      stakersPowBalance: 0,
      stakersPowAllowanceForLeveling: 0,

      stakersWomAllowanceForShop: 0,
      stakersFlexUsdAllowanceForShop: 0,

      stakersBchBalance: 0,
      stakersWomBalance: 0,
      stakersFlexUsdBalance: 0,

      powSellerContract: null,
      powSaleActive: false,

      powTokensToBuy: 100,
      powTokenPriceBch: 0,
      powTokenPriceWom: 0,
      powTokenPriceFlexUsd: 0,

      rewardTokenContract: 0,
      flexUsdTokenContract: 0,

      feedbackMessage: "",
    }
  },
  methods: {
    approvePowToken: async function () {
      let that = this;

      that.feedbackMessage = "Approving POW token";

      let tx = await that.powTokenContract
        .approve(window.StakingAddress, ethers.constants.MaxUint256, {
          gasLimit: 65000, // 60k is probably fine, too
          gasPrice: 1046740000,
        });
      await tx.wait();
      that.powTokenContract.allowance(that.stakersAddress, window.StakingAddress).then((allowance) => {
        that.stakersPowAllowanceForLeveling = allowance;
      });
      that.feedbackMessage = "";
    },

    removePowTokenAllowance: async function () {
      let that = this;

      that.feedbackMessage = "Removing POW token approval";

      let tx = await that.powTokenContract
        .approve(window.StakingAddress, 0, {
          gasLimit: 65000, // 60k is probably fine, too
          gasPrice: 1046740000,
        });
      await tx.wait();
      that.powTokenContract.allowance(that.stakersAddress, window.StakingAddress).then((allowance) => {
        that.stakersPowAllowanceForLeveling = allowance;
      });
      that.feedbackMessage = "";
    },

    // decreasePowTokenAllowance: async function () {
    //   let that = this;

    //   that.feedbackMessage = "Decreasing POW token approval by " + that.powTokensToBuy + " POW";

    //   let tx = await that.powTokenContract
    //     .decreaseAllowance(window.StakingAddress, that.powTokensToBuy, {
    //       gasLimit: 65000, // 60k is probably fine, too
    //       gasPrice: 1046740000,
    //     });
    //   await tx.wait();
    //   that.powTokenContract.allowance(that.stakersAddress, window.StakingAddress).then((allowance) => {
    //     that.stakersPowAllowanceForLeveling = allowance;
    //   });
    //   that.feedbackMessage = "";
    // },
    // increasePowTokenAllowance: async function () {
    //   let that = this;

    //   that.feedbackMessage = "Increasing POW token approval by " + that.powTokensToBuy + " POW";

    //   let tx = await that.powTokenContract
    //     .increaseAllowance(window.StakingAddress, that.powTokensToBuy, {
    //       gasLimit: 65000, // 60k is probably fine, too
    //       gasPrice: 1046740000,
    //     });
    //   await tx.wait();
    //   that.powTokenContract.allowance(that.stakersAddress, window.StakingAddress).then((allowance) => {
    //     that.stakersPowAllowanceForLeveling = allowance;
    //   });
    //   that.feedbackMessage = "";
    // },

    getPowSaleActive: async function () {
      this.powSellerContract.saleActive().then((isActive) => {
        this.powSaleActive = isActive;
        if (!isActive) {
          this.feedbackMessage = "Buying POW Tokens is currently not possible. Please try again later!";
        }
      });
    },

    getPowTokenPrices: async function () {
      this.powSellerContract.powPriceBch().then((priceInWei) => {
        this.powTokenPriceBch = priceInWei;
      });
      this.powSellerContract.getPowPriceToken(window.RewardTokenAddress).then((priceInWei) => {
        this.powTokenPriceWom = priceInWei;
      });
      this.powSellerContract.getPowPriceToken(window.FlexUsdAddress).then((priceInWei) => {
        this.powTokenPriceFlexUsd = priceInWei;
      });
    },

    getPowTokenBalance: function () {
      this.powTokenContract.balanceOf(this.stakersAddress).then((balance) => {
        this.stakersPowBalance = balance;
      });
      this.powTokenContract.allowance(this.stakersAddress, window.StakingAddress).then((allowance) => {
        this.stakersPowAllowanceForLeveling = allowance;
      });
    },

    getFlexUsdBalance: function () {
      this.flexUsdTokenContract.balanceOf(this.stakersAddress).then((balance) => {
        this.stakersFlexUsdBalance = balance;
      });
    },

    getWomBalance: function () {
      this.rewardTokenContract.balanceOf(this.stakersAddress).then((balance) => {
        this.stakersWomBalance = balance;
      });
    },

    getBchBalance: function () {
      this.provider.getBalance(this.stakersAddress).then((balance) => {
        this.stakersBchBalance = balance;
      });
    },


    buyPowWithBch: async function () {
      let powTokensInt = Math.floor(this.powTokensToBuy);
      if (powTokensInt !== this.powTokensToBuy) {
        this.feedbackMessage = "Only integers allowed!";
        this.powTokensToBuy = powTokensInt;
        return false;
      }
      if (this.powTokensToBuy < 10) {
        this.feedbackMessage = "You need to buy at least 10 POW tokens";
        return false;
      } 
  
      let bchValue = this.powTokensToBuy * this.powTokenPriceBch / 10 ** 18;
      if (bchValue > this.stakersBchBalance/10**18) {
        this.feedbackMessage = "Not enough BCH";
        return false;
      }

      if (bchValue <= 0) {
        this.feedbackMessage = "BCH payments are currently not accepted";
        return false;
      }

      this.feedbackMessage = "Buying " + this.powTokensToBuy + " POW tokens";
      const options = { value: ethers.utils.parseEther(bchValue.toString()) };
      let tx = await this.powSellerContract.buyPowWithBch(this.powTokensToBuy, options);
      await tx.wait();
      this.getPowTokenBalance();
      this.feedbackMessage = "";
    },

    buyPowWithToken: async function (tokenName) {
      let that = this;

      let powTokensInt = Math.floor(that.powTokensToBuy);
      if (powTokensInt !== that.powTokensToBuy) {
        that.feedbackMessage = "Only integers allowed!";
        that.powTokensToBuy = powTokensInt;
        return false;
      }


      let powTokenPrice = 0;
      let stakersAllowance = 0;
      let stakersBalance = 0;
      let tokenContract = 0;
      let tokenCA = 0;
      if (tokenName === 'WoM') {
        powTokenPrice = that.powTokenPriceWom;
        stakersAllowance = that.stakersWomAllowanceForShop;
        stakersBalance = that.stakersWomBalance;
        tokenContract = that.rewardTokenContract;
        tokenCA = window.RewardTokenAddress;
      } else if (tokenName === 'flexUSD') {
        powTokenPrice = that.powTokenPriceFlexUsd;
        stakersAllowance = that.stakersFlexUsdAllowanceForShop;
        stakersBalance = that.stakersFlexUsdBalance;
        tokenContract = that.flexUsdTokenContract;
        tokenCA = window.FlexUsdAddress;
      } else {
        that.feedbackMessage = tokenName + " purchases are not possible right now. Please try another payment method.";
        return false;
      }

      let tokenValue = that.powTokensToBuy * powTokenPrice;
      // console.log(that.stakersWomAllowanceForShop);

      if (tokenValue > stakersBalance) {
        that.feedbackMessage = "Not enough " + tokenName;
        return false;
      }

      if (tokenValue <= 0) {
        this.feedbackMessage = tokenName + " payments are currently not accepted";
        return false;
      }

      if (tokenValue > stakersAllowance) {
        that.feedbackMessage = "Approving " + tokenName + " first...";

        let tx = await tokenContract
          .approve(window.PowSellerAddress, ethers.constants.MaxUint256, {
            gasLimit: 65000, // 60k is probably fine, too
            gasPrice: 1046740000,
          });
        await tx.wait();
        stakersAllowance = await tokenContract.allowance(that.stakersAddress, window.PowSellerAddress);
        if (tokenValue > stakersAllowance) {
          that.feedbackMessage = "Approving " + tokenName + " failed. Try again later...";
          return false;
        }
      }

      if (that.powTokensToBuy < 10) {
        that.feedbackMessage = "You need to buy at least 10 POW tokens";
        return false;
      } else {
        that.feedbackMessage = "Buying " + that.powTokensToBuy + " POW tokens";
      }
      let tx = await that.powSellerContract.buyPowWithTokens(that.powTokensToBuy, tokenCA, {
        gasLimit: 120000, // 60k is probably fine, too
        gasPrice: 1046740000,
      });
      await tx.wait();
      that.getPowTokenBalance();
      that.feedbackMessage = "";
    },

    getAllowances: function () {
      let that = this;
      that.rewardTokenContract.allowance(that.stakersAddress, window.PowSellerAddress).then((allowance) => {
        that.stakersWomAllowanceForShop = allowance;
        // console.log(that.stakersWomAllowanceForShop);
      });
    },

    load: async function () {
      this.signer.getAddress().then((myAddress) => {
        this.stakersAddress = myAddress;
        this.getPowTokenBalance();
        this.getBchBalance();
        this.getWomBalance();
        this.getFlexUsdBalance();
        this.getAllowances();
      });
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    this.provider.send("eth_requestAccounts", []).then(() => {
      this.signer = this.provider.getSigner();

      let _powSellerContract = new ethers.Contract(window.PowSellerAddress, PowTokenSeller.abi, this.provider.getSigner());
      let _powTokenContract = new ethers.Contract(
        window.PowTokenAddress,
        PowToken.abi,
        this.provider.getSigner()
      );
      let _womContract = new ethers.Contract(window.RewardTokenAddress, RewardToken.abi, this.provider.getSigner());
      let _flexUsdContract = new ethers.Contract(window.FlexUsdAddress, RewardToken.abi, this.provider.getSigner());

      this.powSellerContract = _powSellerContract;
      this.powTokenContract = _powTokenContract;
      this.rewardTokenContract = _womContract;
      this.flexUsdTokenContract = _flexUsdContract;
    }).then(() => {
      this.load();
      this.getPowSaleActive();
      this.getPowTokenPrices();
    })
  }
}
</script>