<template>
  <div class="content-body">
    <div class="container">
      <div class="row">
        <div class="col-xxl-12">
          <h2>NFT Minting</h2>
          <p>
            Mint some World of Masks NFTs on the testnet for free!
            <button @click="mintWom(49)" class="btn btn-primary">Mint 50</button>
            &nbsp;
            <button @click="mintWom(24)" class="btn btn-primary">Mint 25</button>
            &nbsp;
            <button @click="mintWom(5)" class="btn btn-primary">Mint 5</button>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { ethers } from "ethers";
import WorldOfMasksNFT from "../../artifacts/contracts/WorldOfMasksNFT.sol/WorldOfMasksNFT.json";


export default {
  name: 'App',
  components: {},
  data() {
    return {
      provider: null,
    }
  },
  methods: {
    mintWom: async function (amount) {
      let c = new ethers.Contract(window.WomNftAddress, WorldOfMasksNFT.abi, this.provider.getSigner());
      let tx = await c.mint(amount, {
        gasLimit: 9999999,
        gasPrice: 1046740000,
      });
      await tx.wait();
      window.location.reload();
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    this.provider.send("eth_requestAccounts", []).then(() => {
      this.signer = this.provider.getSigner();
    });
  }
}
</script>