<template>
  <div class="header">
    <div class="container">
      <div class="row">
        <div class="col-xxl-12">
          <div class="header-content">
            <div class="header-left">
              <div class="brand-logo">
                <a class="mini-logo" href="./">
                  <img src="images/logo.png" alt="" width="40" />
                </a>
              </div>
            </div>
            <div class="header-right">
              <!-- <div class="dark-light-toggle theme-switch" onclick="themeToggle()">
                <span class="dark"><i class="ri-moon-line"></i></span>
                <span class="light"><i class="ri-sun-line"></i></span>
              </div> -->
              <template v-if="!myAddressDisplay && !wrongNetwork">
              <div class="dark-light-toggle theme-switch">
                <a @click="addWomToMM" class="btn btn-outline-secondary btn-sm"><img
                    src="images/metamask-icon-32.png" /> Add WoM</a>
              </div>
              <div class="dark-light-toggle theme-switch">
                <a @click="addPowToMM" class="btn btn-outline-secondary btn-sm"><img
                    src="images/metamask-icon-32.png" /> Add POW</a>
              </div>
              </template>
              <div class="dark-light-toggle theme-switch" v-if="!myAddressDisplay && !wrongNetwork">
                <a @click="loginMM" class="btn btn-primary btn-sm"><img src="images/metamask-icon-32.png" /> Login</a>
              </div>
              <div class="dark-light-toggle theme-switch" v-if="wrongNetwork">
                <strong>Please switch the network to {{ netName }}.</strong>
              </div>
              <div class="dropdown profile_log dropdown">
                <div data-bs-toggle="dropdown" aria-haspopup="true" class="" aria-expanded="false">
                  <div class="user icon-menu active loggeed-in-addr-text">
                    <span>{{ myAddressDisplay }}</span>
                  </div>
                </div>
                <div tabindex="-1" role="menu" aria-hidden="true" class="dropdown-menu dropdown-menu-right">
                  <a class="dropdown-item" href="./shop">
                    <span><i class="ri-shopping-cart-2-fill"></i></span>POW Shop
                  </a>
                  <a class="dropdown-item" href="./wom">
                    <span><i class="ri-user-3-fill"></i></span>My World of Masks NFTs
                  </a>                  
                  <a class="dropdown-item" v-bind:href="
                    'https://www.smartscan.cash/address/' + stakingAddress
                  ">
                    <span><i class="ri-search-eye-line"></i></span>View Staking Contract Activity
                  </a>
                  <div class="dark-light-toggle theme-switch">
                <a @click="addWomToMM" class="btn btn-outline-secondary btn-sm"><img
                    src="images/metamask-icon-32.png" /> Add WoM</a>
              </div>
              <div class="dark-light-toggle theme-switch">
                <a @click="addPowToMM" class="btn btn-outline-secondary btn-sm"><img
                    src="images/metamask-icon-32.png" /> Add POW</a>
              </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="sidebar">
    <div class="brand-logo">
      <a class="full-logo" href="./"><img src="images/logo.png" alt="" width="40" /></a>
    </div>
    <div class="menu">
      <ul>
         <li>
          <a href="./nftMint">
            <span><i class="ri-gift-fill"></i></span>
            <span class="nav-text">NFT Mint</span>
          </a>
        </li>
        <li>
          <a href="./shop">
            <span><i class="ri-shopping-cart-2-fill"></i></span>
            <span class="nav-text">POW Shop</span>
          </a>
        </li>
        <li>
          <a href="./wom">
            <span><i class="ri-user-3-fill"></i></span>
            <span class="nav-text">NFTs</span>
          </a>
        </li>
        <!-- <li>
          <a href="./admin">
            <span><i class="ri-settings-3-fill"></i></span>
            <span class="nav-text">Admin</span>
          </a>
        </li> -->
      </ul>
    </div>
  </div>
</template>
<script>
import { ethers } from "ethers";
export default {
  data() {
    return {
      myAddressDisplay: null,

      stakingAddress: window.StakingAddress,
      netName: window.netName,
      wrongNetwork: false,
    };
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider.getNetwork().then((network) => {
      if (network.chainId !== window.netId) {
        this.wrongNetwork = true;
        window.ethereum.request({
          method: 'wallet_switchEthereumChain',
          params: [{ chainId: ethers.utils.hexlify(window.netId) }]
        })
          .catch((err) => {
            // This error code indicates that the chain has not been added to MetaMask
            if (err.code === 4902) {
              // dispatch(connectFailed(`Add ${window.netName} to Metamask and then reload this website.`));
              window.ethereum.request({
                method: 'wallet_addEthereumChain',
                params: [
                  {
                    chainName: window.netName,
                    chainId: ethers.utils.hexlify(window.netId),
                    nativeCurrency: { name: "BCH", decimals: 18, symbol: "BCH" },
                    rpcUrls: [window.rpcServer],
                    blockExplorerUrls: ["https://smartscan.cash/"]
                  }
                ]
              });
            }
          });
      }
    });
    this.provider.on("network", (n, o) => {
      if (o) {
        window.location.reload();
      }
    });
    this.signer = this.provider.getSigner();
    this.signer.getAddress().then((address) => {
      this.myAddressDisplay =
        address.substring(0, 5) +
        "..." +
        address.substring(address.length - 5, address.length);
    });
  },
  methods: {
    loginMM: function () {
      window.location.reload();
    },
    addPowToMM: function () {
      window.ethereum.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20",
          options: {
            address: window.PowTokenAddress,
            symbol: "POW",
            decimals: 0,
          },
        },
      });
    },
    addWomToMM: function () {
      window.ethereum.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20",
          options: {
            address: window.RewardTokenAddress,
            symbol: "WoM",
            decimals: 18,
          },
        },
      });
    },
  },
};
</script>


<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 60px;
}
</style>